.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;

  background-color: rgba(0, 0, 0, 0.5);
}


.modal__window {
  position: fixed;
  top: 50%;
  right: 20px;
  left: 20px;

  padding: 20px;
  // background-color: lightgrey;

  background-color: #ffffff;
  border-radius: 10px;
  transform: translateY(-50%);


  @media (min-width: 480px) {
    left: 50%;

    width: 440px;

    transform: translate(-50%, -50%);
  }
}


.modal__button {
  display: block;
  padding: 0;
  padding: 10px;

  font: inherit;
  color: inherit;
  color: $mtc;

  background-color: $ac;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}


.modal__close-button {
  position: fixed;
  top: 10px;
  right: 10px;

  width: 50px;
  height: 50px;
  padding: 0;

  font: inherit;
  color: inherit;

  background-color: transparent;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  transition: 0.2s ease;

  &:hover {
    background-color: rgba(0, 0, 0, 0.15);
  }

  &:active {
    opacity: 0.5;
  }
}
